*,
*:after,
*:before {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-color: white;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: black;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1010px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.default-title {
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: #121212;
}

.section {
  width: 100%;
  height: 600px;
  color: white;
  text-align: center;
}
.section.lightblue {
  background-color: lightblue;
}
.section.orange {
  background-color: orange;
}

.content {
  max-width: 1440px;
  width: 100%;
  margin: 100px auto;
}
.content-inner {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.content__img {
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
}
.content__img-wrapper {
  width: 100%;
  height: 330px;
  max-width: 505px;
}
.content__info {
  max-width: 380px;
  width: 100%;
}
.content__info-title {
  margin-bottom: 24px;
}
.content__info-text {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: #121212;
  margin-bottom: 32px;
}
.content__info-btn {
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #FFFFFF;
  padding: 16px 40px;
  background: #E4C1AE;
  border-radius: 32px;
  border: 0;
  cursor: pointer;
  transition: 0.3s;
}
.content__info-btn:hover {
  background: #b48575;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
}
.popup-overlay {
  background-color: rgba(18, 18, 18, 0.5);
  width: 100%;
  height: 100%;
  position: relative;
  display: grid;
  place-items: center;
  transition: 0.5s;
}
.popup-window {
  max-width: 920px;
  background-color: white;
  width: 100%;
  padding: 56px 95px;
  position: relative;
  transition: 0.5s;
}
.popup-inner {
  width: 100%;
}
.popup-title {
  margin-bottom: 24px;
}
.popup-text {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: #121212;
}
.popup-close {
  width: 24px;
  height: 24px;
  position: absolute;
  top: 40px;
  right: 40px;
  cursor: pointer;
}
.popup .reset {
  margin-top: 24px;
  display: block;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #FFFFFF;
  padding: 16px 40px;
  background: #E4C1AE;
  border-radius: 32px;
  border: 0;
  cursor: pointer;
  transition: 0.3s;
}
.popup .reset:hover {
  background: #b48575;
}
.popup .reset.hidden {
  display: none;
}
.popup.hidden {
  visibility: hidden;
}
.popup.hidden .popup-overlay {
  background-color: rgba(18, 18, 18, 0);
}
.popup.hidden .popup-window {
  transform: translateY(-100vh);
}

@media screen and (max-width: 930px) {
  .content-inner {
    flex-direction: column;
  }
  .content__info {
    margin-top: 50px;
    max-width: 505px;
  }
  .content__info-btn {
    display: block;
    margin: 0 auto;
  }
}